<template>
  <div>
    <div class="container">
      <div class="workNav">
        <ul>
          <li v-for="(nav, index) in workNav" :key="index" @click="changeCat(nav.data)">
            {{ nav.name }}
          </li>
        </ul>
      </div>
      <div class="workContainer">
        <transition-group>
          <ul v-for="(project, index) in projects" :key="index">
            <li v-if="projectsToShow == project.category">
              <div class="project" @mouseenter="project.showTags=true" @mouseleave="project.showTags=false">
                <h6 class="title"><a :href="project.link" target="_blank" rel="noreferrer nofollow noopener">{{ project.name }}</a></h6>
                <div class="tags">
                  <transition name="tagsIn">
                    <ul v-if="project.showTags">
                      <li v-for="(tag, index) in project.tags" :key="index">
                        {{ tag }}
                      </li>
                    </ul>
                  </transition>
                </div>
                <a class="footer" :href="project.github" target="_blank" rel="nofollow noreferrer noopener">
                  <img :src="githubIcon" alt="Github Icon" width="20">
                  <h6 class="title">Github</h6>
                </a>
              </div>
            </li>
            <li v-else-if="projectsToShow == 'all'">
              <div class="project" @mouseenter="project.showTags=true" @mouseleave="project.showTags=false">
                <h6 class="title"><a :href="project.link" target="_blank" rel="noreferrer nofollow noopener">{{ project.name }}</a></h6>
                <div class="tags">
                  <transition name="tagsIn">
                    <ul v-if="project.showTags">
                      <li v-for="(tag, index) in project.tags" :key="index">
                        {{ tag }}
                      </li>
                    </ul>
                  </transition>
                </div>
                <a class="footer" :href="project.github" target="_blank" rel="nofollow noreferrer noopener">
                  <img :src="githubIcon" alt="Github Icon" width="20">
                  <h6 class="title">Github</h6>
                </a>
              </div>
            </li>
          </ul>
        </transition-group>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      projectsToShow: 'all',
      githubIcon: 'static/Images/Social/gh.png',
      workNav: [
        {
          name: 'all',
          info: '',
          data: 'all'
        },
        {
          name: 'responsive design',
          info: '',
          data: 'resp'
        },
        {
          name: 'java script',
          info: '',
          data: 'js'
        },
        {
          name: 'php',
          info: '',
          data: 'php'
        }
      ],
      projects: [
        {
          name: 'Kat FMWorld UK',
          info: 'My first commercial project',
          link: 'https://eager-franklin-097a07.netlify.com',
          github: 'https://github.com/mru24/FM_World_UK',
          tags: ['HTML', 'VUE JS', 'SASS', 'Bootstrap'],
          showTags: false,
          category: 'js'
        },
        {
          name: 'My Portfolio',
          info: 'This Portfolio',
          link: 'https://vigilant-shirley-59d618.netlify.com',
          github: 'https://github.com/mru24/Yet-another-Portfolio',
          tags: ['HTML', 'VUE JS', 'SASS', 'Firebase'],
          showTags: false,
          category: 'js'
        },
        {
          name: 'PropertiesHaven',
          info: '',
          link: 'https://nifty-bardeen-3df8c6.netlify.com/',
          github: 'https://github.com/mru24/PropertiesHaven',
          tags: ['HTML', 'SCSS', 'jQUERY'],
          showTags: false,
          category: 'resp'
        },
        {
          name: 'Profile Widget',
          info: '',
          link: 'https://jolly-fermi-d95467.netlify.com/',
          github: 'https://github.com/mru24/Portfolio-Widget',
          tags: ['HTML', 'SCSS', 'Bootstrap', 'jQUERY'],
          showTags: false,
          category: 'resp'
        },
        {
          name: 'School',
          info: '',
          link: 'https://musing-hermann-a64cac.netlify.com/',
          github: 'https://github.com/mru24/School',
          tags: ['HTML', 'SCSS', 'Bootstrap', 'jQUERY'],
          showTags: false,
          category: 'resp'
        },
        {
          name: 'Ping - JS Game',
          info: '',
          link: 'https://pensive-carson-b3e5e8.netlify.com/',
          github: 'https://github.com/mru24/Ping_JS_Game',
          tags: ['JavaScript', 'HTML', 'SASS'],
          showTags: false,
          category: 'js'
        },
        {
          name: 'School',
          info: '',
          link: 'https://musing-hermann-a64cac.netlify.com/',
          github: 'https://github.com/mru24/School',
          tags: ['HTML', 'SCSS', 'Bootstrap', 'jQUERY'],
          showTags: false,
          category: 'resp'
        },
        {
          name: 'Ping - JS Game',
          info: '',
          link: 'https://pensive-carson-b3e5e8.netlify.com/',
          github: 'https://github.com/mru24/Ping_JS_Game',
          tags: ['JavaScript', 'HTML', 'SASS'],
          showTags: false,
          category: 'js'
        }
      ]
    }
  },
  methods: {
    changeCat: function (data) {
      this.projectsToShow = data
    },
    showTags: function () {
      this.projects.showTags = true
    },
    hideTags: function () {
      this.projects.showTags = false
    }
  }
}
</script>

<style lang="sass" scoped>
@import 'config'

.container
  height: auto
  min-height: 100vh
  .workNav
    width: 80%
    margin: 30px auto
    border-bottom: $borderLt
    ul
      display: flex
      flex-direction: row
      justify-content: space-around
      @include bp-mobileSM
        flex-direction: column
        justify-content: flex-start
        align-items: flex-start
      li
        text-transform: capitalize
        cursor: pointer
  .workContainer
    height: auto
    span
      display: flex
      flex-direction: row
      flex-wrap: wrap
      justify-content: center
      ul
        transition: .5s
        li
          .project
            background: rgba($bgColor, 0.8)
            width: 200px
            height: 260px
            border: $borderLt
            padding: 5px
            margin: 10px
            overflow: hidden
            position: relative
            @include bp-mobile
              width: 170px
              height: 250px
              margin: 5px
            .title
              font-size: 18px
              font-weight: 200
            .tags
              position: absolute
              top: 50%
              left: 0
              width: 100%
              display: flex
              align-items: center
              opacity: .5
              ul
                width: 100%
                display: flex
                flex-direction: row
                flex-wrap: wrap
                justify-content: center
                li
                  border: $borderLt
                  padding: 2px 5px
                  font-size: 10px
                  margin: 4px
            .footer
              position: absolute
              bottom: 5px
              left: 10px
              display: flex
              flex-direction: row
              align-items: center
              .title
                margin-left: 10px

.tagsIn-enter-active, .tagsIn-leave-active
  transition: all .5s easy-in-out
.tagsIn-enter, .tagsIn-leave-to
  transform: scale(0)
  opacity: 0

</style>



